.crops-page {
  padding: var(--spacing-lg);
  background-color: var(--white);
  min-height: calc(100vh - 64px);
}

.crops-header {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-md);
  margin-bottom: var(--spacing-xl);
}

@media (min-width: 600px) {
  .crops-header {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.crops-title {
  color: var(--primary-green);
  font-size: var(--font-size-2xl);
  font-weight: 600;
  margin: 0;
}

.new-crop-button {
  background-color: var(--primary-green) !important;
  border-radius: var(--border-radius) !important;
  padding: var(--spacing-sm) var(--spacing-xl) !important;
  font-size: var(--font-size-base) !important;
  text-transform: none !important;
  box-shadow: var(--shadow-md) !important;
  transition: all 0.3s ease !important;
}

.new-crop-button:hover {
  background-color: var(--dark-green) !important;
  box-shadow: var(--shadow-lg) !important;
}

.search-container {
  background-color: var(--white);
  padding: var(--spacing-lg);
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-sm);
  margin-bottom: var(--spacing-xl);
}

.search-input {
  border-color: var(--medium-gray);
}

/* Personalizando el campo de búsqueda */
.search-input .MuiOutlinedInput-root {
  transition: all 0.3s ease;
}

.search-input .MuiOutlinedInput-root:hover .MuiOutlinedInput-notchedOutline {
  border-color: var(--primary-green);
}

.search-input .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline {
  border-color: var(--primary-green);
  border-width: 2px;
  box-shadow: 0 0 0 4px rgba(76, 175, 80, 0.1);
}

.crops-table-container {
  background-color: var(--white);
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-md);
  overflow: hidden;
}

.crops-table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
}

.crops-table th {
  background-color: var(--light-gray);
  color: var(--primary-green);
  font-weight: 600;
  padding: var(--spacing-md);
  text-align: left;
  border-bottom: 2px solid var(--medium-gray);
}

.crops-table td {
  padding: var(--spacing-md);
  border-bottom: 1px solid var(--medium-gray);
}

.crops-table tr:hover {
  background-color: var(--light-gray);
}

.status-chip {
  padding: var(--spacing-xs) var(--spacing-md);
  border-radius: var(--border-radius-sm);
  font-size: var(--font-size-sm);
  font-weight: 500;
}

.status-chip.sembrado {
  background-color: #E3F2FD;
  color: #1976D2;
}

.status-chip.en_crecimiento {
  background-color: #FFF3E0;
  color: #ED6C02;
}

.status-chip.cosechado {
  background-color: #E8F5E9;
  color: var(--primary-green);
}

.status-chip.perdido {
  background-color: #FFEBEE;
  color: #D32F2F;
}

.action-button {
  padding: var(--spacing-xs) !important;
  margin: 0 var(--spacing-xs) !important;
}

.edit-button {
  color: var(--primary-green) !important;
}

.delete-button {
  color: #D32F2F !important;
}

/* Estilos para el Modal */
.crop-modal {
  border-radius: var(--border-radius-lg) !important;
  overflow: hidden;
}

.modal-header {
  background-color: var(--primary-green);
  color: var(--white);
  padding: var(--spacing-lg) var(--spacing-xl);
}

.modal-content {
  padding: var(--spacing-xl);
}

.modal-form-field {
  margin-bottom: var(--spacing-lg) !important;
}

.modal-form-field label.Mui-focused {
  color: var(--primary-green);
}

.modal-form-field .MuiOutlinedInput-root:hover .MuiOutlinedInput-notchedOutline {
  border-color: var(--primary-green);
}

.modal-form-field .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline {
  border-color: var(--primary-green);
}

.modal-actions {
  padding: var(--spacing-lg) var(--spacing-xl);
  background-color: var(--light-gray);
}

.modal-cancel-button {
  color: var(--dark-gray) !important;
}

.modal-save-button {
  background-color: var(--primary-green) !important;
  color: var(--white) !important;
  padding: var(--spacing-sm) var(--spacing-xl) !important;
}

.modal-save-button:hover {
  background-color: var(--dark-green) !important;
}

@media (max-width: 600px) {
  .crops-page {
    padding: var(--spacing-md);
  }

  .crops-table-container {
    margin: 0 calc(-1 * var(--spacing-md));
    border-radius: 0;
    overflow-x: auto;
  }

  .crops-table th,
  .crops-table td {
    padding: var(--spacing-sm);
    font-size: var(--font-size-sm);
  }

  .new-crop-button {
    width: 100%;
  }

  .search-container {
    padding: var(--spacing-md);
  }
}

.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 400px;
}

.loading-spinner {
  color: var(--primary-green) !important;
}